.App {
  text-align: center;
}
* {
  margin: 0px;
  padding: 0px;
}
.todoItem {
  display: flex;
  align-items: start;
  background-color: #002884;
  color: white;
  padding: 10px;
  margin: 5px 10px;
  text-align: left;
  border-radius: 3px;
}
.todoItem h3 {
  letter-spacing: 0.5px;
  text-transform: capitalize;
  font-weight: 700;
  font-size: 18px;
  line-height: 1.2;
}
.todoItem p {
  text-transform: none;
  transition: 0.5s;
  font-size: 15px;
  padding-top: 5px;
}
.todoItem:hover {
  & p {
    padding: 10px 5px;
  }
}

/* ======================= */
body {
  margin: auto;
  font-family: -apple-system, BlinkMacSystemFont, sans-serif;
  overflow: auto;
  background: linear-gradient(
    315deg,
    rgba(101, 0, 94, 1) 3%,
    rgba(60, 132, 206, 1) 38%,
    rgb(17, 87, 83) 68%,
    rgba(255, 25, 25, 1) 98%
  );
  animation: gradient 15s ease infinite;
  background-size: 400% 400%;
  background-attachment: fixed;
}

@keyframes gradient {
  0% {
    background-position: 0% 0%;
  }
  50% {
    background-position: 100% 100%;
  }
  100% {
    background-position: 0% 0%;
  }
}

.wave {
  background: rgb(255 255 255 / 25%);
  border-radius: 1000% 1000% 0 0;
  position: fixed;
  width: 200%;
  height: 12em;
  animation: wave 10s -3s linear infinite;
  transform: translate3d(0, 0, 0);
  opacity: 0.8;
  bottom: 0;
  left: 0;
  z-index: -1;
}

.wave:nth-of-type(2) {
  bottom: -1.25em;
  animation: wave 18s linear reverse infinite;
  opacity: 0.8;
}

.wave:nth-of-type(3) {
  bottom: -2.5em;
  animation: wave 20s -1s reverse infinite;
  opacity: 0.9;
}

@keyframes wave {
  2% {
    transform: translateX(1);
  }

  25% {
    transform: translateX(-25%);
  }

  50% {
    transform: translateX(-50%);
  }

  75% {
    transform: translateX(-25%);
  }

  100% {
    transform: translateX(1);
  }
}
/* ======================= */